import axios from 'axios'

axios.defaults.baseURL = 'http://127.0.0.1:3000/'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export const expressAjax   = params => { return axios.get(`/blog/adminX`, { params: params }).then(res => res.data); };

export const expresspost = params => { return axios.post(`/post`,params).then(resolve => resolve.data) }



